import ScrollToBottom from 'react-scroll-to-bottom';
import moment from 'moment';
import { useLocation } from 'react-router-dom';
import PrimaryButton from '../../../widgets/Button/Button';
import { LoadingDots } from '../../../widgets/Loader/Loader';
import s from './Notification.module.scss';
import get from 'lodash/get';
import { getFirstN } from '../../../hooks';
import { sortByTime } from '../../../utils';

export const NotificationsJSX = ({
    queries,
    user,
    excludeText,
    respondToQuery,
    type='other',
    lastMessages = {}
}) => {

    const anim = [s.beat, s.pulse].join(' ');
    const { hash } = useLocation();

    const displayLast = (id, title) => {
        const last = Boolean(get(lastMessages, [id, 'by'])) 
        ? get(lastMessages, [id]) 
        : { by: 'Admin', content: `This chat is about ${title}` };
        
        return <> 
            <span className={s.main_item_text_time}>
                {last.by}: {getFirstN(last.content, 25)}
            </span>
            {last.at ? <span className={s.main_item_text_time}>{moment(last.at).fromNow()}</span> : <></>}
        </>;
    };
    
    return <>
        <ScrollToBottom mode='top'>            
            {
                (lastMessages ? sortByTime(queries, lastMessages) : queries).map(q => <div 
                    className={[
                        s.main_item, q.response === 'ended' ? s.ended : '', 
                        (hash !== `#${q.id}` && get(lastMessages, [q.id, 'highlight'], null)) ? anim : '',
                        hash === `#${q.id}` ? s.highlight : '',
                        (q.response === 'ended' && q.answeredBy !== user.id) ? s.main_item_dark : ''
                    ].join(' ')} 
                    key={q.id} onClick={() => {
                        if(q.answeredBy === user.id) respondToQuery(q)
                    }}
                >
                    <span className={s.main_item_text}>
                        {displayLast(q.id, q.title)}
                    </span>
                    {((hash !== `#${q.id}` || type !== 'my') && q.answeredBy !== user.id) ? <PrimaryButton 
                        clickable={excludeText.id !== q.id} className={s.main_item_button}
                        text={excludeText.id === q.id ? <LoadingDots /> : 'Respond'}
                        onProceed={() => respondToQuery(q)}
                    /> : <></>}
                </div>)
            }
        </ScrollToBottom>
    </>
};
